.language-inner {
  width: 100vw;
  height: 100vh;
  position: relative;
  overflow: hidden;
}

.language-bg-container {
  box-sizing: border-box;
  flex-direction: column;
  justify-content: space-between;
  width: 100vw;
  height: 100vh;
  padding: 2rem;
  display: flex;
  position: absolute;
  overflow: hidden;
}

.language-bg-text-1 {
  color: var(--bg-text-color);
  font-size: 12em;
  font-weight: bold;
  transform: translate(200%);
}

.language-bg-text-2 {
  color: var(--bg-text-color);
  align-self: flex-end;
  font-size: 12em;
  font-weight: bold;
  transform: translate(-200%);
}

.language-boxs-outer {
  box-sizing: border-box;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 3rem;
  width: 100vw;
  height: 100vh;
  display: flex;
  position: absolute;
}

.language-boxs-inner {
  grid-template-columns: 1fr 1fr 1fr;
  gap: 3rem;
  width: 65rem;
  height: 15rem;
  display: grid;
}

.language-boxs-inner:first-child {
  margin-right: 12rem;
}

.language-boxs-inner:nth-child(2) {
  margin-left: 12rem;
}

.language-box-inner {
  opacity: 0;
  outline: 2px solid #fff;
  flex-direction: column;
  justify-content: space-between;
  padding: 2rem;
  display: flex;
}

.language-box-title {
  color: #fff;
  font-size: 2.5em;
  font-weight: 600;
}

.language-box-inner > section {
  gap: 1rem;
  display: grid;
}

.language-box-level {
  color: var(--gray-text-color);
  font-size: 1.2em;
}

.language-box-level-bar {
  height: 1rem;
}

#nodejs-level-bar {
  background-image: linear-gradient(135deg, #fff 90%, #ffffff41 10%);
}

#js-level-bar, #html-level-bar, #sql-level-bar, #mongodb-level-bar {
  background-image: linear-gradient(135deg, #fff 70%, #ffffff41 30%);
}

#rust-level-bar {
  background-image: linear-gradient(135deg, #fff 50%, #ffffff41 50%);
}

